import { lazy, Suspense, useEffect, useState } from "react";
import "./App.css";
const Content = lazy(() => import("./components/js files/Content"));
const renderLoader = () => <h4>loading</h4>;

function App() {
  const [content, setContent] = useState([]);
  const [pageno, setPageno] = useState(1);
  const [page, setPage] = useState("airing");
  const [searchi, setSearchi] = useState(false);

  const activate = () => {
    const pages = document.querySelectorAll(".navs");
    pages.forEach((element) => {
      element.style.background = "transparent";
    });

    const activePage = document.getElementById(page);
    activePage.style.background = "#d1a6e7";
  };

  const prevV = () => {
    const nextBtn = document.getElementById("next");
    const prevBtn = document.getElementById("previous");
    if (pageno === 1 || page === "search-btn") {
      prevBtn.style.display = "none";
    } else {
      prevBtn.style.display = "block";
    }

    if ((page === "airing" && pageno === 6) || page === "search-btn") {
      nextBtn.style.display = "none";
    } else {
      nextBtn.style.display = "block";
    }
  };
  const Search = async () => {
    const searched = document.getElementById("search-box").value;
    if (searched !== "") {
      const searchedResult = await fetch(
        ` https://api.jikan.moe/v3/search/anime?q=${searched}/`
      )
        .then((response) => response.json())
        .then((data) => data);
      setContent(searchedResult.results);
    }
  };

  const getContent = async () => {
    if (page !== "search-btn") {
      const list = await fetch(
        `https://api.jikan.moe/v3/top/anime/${pageno}/${page}`
      )
        .then((response) => response.json())
        .then((data) => data);
      if (page === "airing" && pageno === 6) {
        setContent(list.top.slice(0, 40));
      } else {
        setContent(list.top);
      }
    }
  };

  useEffect(() => {
    const next = document.getElementById("next");
    next.style.display = "none";
    const previous = document.getElementById("previous");
    previous.style.display = "none";
    Search();
  }, [searchi]);

  useEffect(() => {
    prevV();
    activate();
    getContent();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page, pageno]);

  return (
    <div
      className="App"
      style={{ flexDirection: "column", alignItems: "center" }}
    >
      <div className="topbar">
        <a href="# " id="title">
          <div style={{ width: "100vw" }}>ANIMINE </div>
        </a>

        <div className="searching" id=" ">
          <input
            type="text"
            placeholder="search for an anime!"
            id="search-box"
          />
          <button
            id="search-btn"
            onClick={() => {
              setPage("search-btn");
              setSearchi(!searchi);
            }}
          >
            search
          </button>
        </div>
        <div className="navig">
          <button
            className="navs"
            id="airing"
            style={{ backgroundColor: " #d1a6e7" }}
            onClick={() => {
              setPage("airing");
              setPageno(1);
            }}
          >
            Airing Now
          </button>
          <button
            className="navs"
            id="upcoming"
            onClick={() => {
              setPage("upcoming");
              setPageno(1);
            }}
          >
            Upcoming Anime
          </button>
          <button
            className="navs"
            id="bypopularity"
            onClick={() => {
              setPage("bypopularity");
              setPageno(1);
            }}
          >
            Most popular
          </button>
        </div>
      </div>
      <div style={{ flexDirection: "column", alignItems: "center" }}>
        <Suspense fallback={renderLoader()}>
          <Content content={content} />
        </Suspense>
      </div>
      <div>
        <a href="# ">
          <button
            className="pages"
            id="previous"
            onClick={() => setPageno(pageno - 1)}
          >
            Previous
          </button>
        </a>
        <a href="# ">
          <button
            className="pages"
            id="next"
            onClick={() => setPageno(pageno + 1)}
          >
            Next
          </button>
        </a>
      </div>
    </div>
  );
}

export default App;
